.dropdown {
    color: white;
    background-color: black;
    position: absolute;
    left: 1.2rem;
    top: 62px;
    border: 3px solid white;
    padding:7px;  
   
    
}

.dropdown:hover {
    opacity: 0.8;
    cursor: pointer;
    background-color: darkorange;
    transition: 0.2s; 
}